import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class NoticiasService {
  autor = [
    'Angela',
    'Antonio',
    'João',
    'Marcos',
    'Anonimo'
  ]
  dados = []

  constructor() { }

  listaDeAutores() {
    return this.autor
  }
  listaDeNoticias() {
    return this.dados
  }

  consulta(id) {
    return this.dados.find(noticia => noticia.id === id);
  }

  consultaAutor(valor) {
    let aux = [];
    for (let noticia of this.dados){
      if (noticia.autor === valor){
        aux.push(noticia)
      }
    }
    return aux
  }

  salvarNoticia(autor, titulo, noticia) {
    let n = {
      'id': Math.random(),
      'autor': autor,
      'titulo': titulo,
      'noticia': noticia,
      'publicar': false,
      'views': 0
    };
    this.dados.push(n)
  }

  excluir = (noticia) => {
    if (confirm("Você realmente que excluir essa notícia '"+ noticia.titulo +  "' ?")) 
    this.dados = this.dados.filter(element => element.id != noticia.id)
  }

  publicarNoticia(valor) {
    for (let dado of this.dados) {
      if (dado.id === valor.id) {
        dado.publicar = true;
      }
    }
  }

  aumentarViews(valor) {
    for (let noticia of this.dados) {
      if (noticia.id === valor.id) {
        noticia.views++
      }
    }
  }

  noticiasPorAutor() {
    let estatisticas = [];
    let aux = {};
    for (let x of this.autor) {
      aux[x] = estatisticas.length;
      estatisticas.push({ autor: x, valor: 0 })
    }
    for (let y of this.dados) {
      if (y.publicar === true){
        estatisticas[aux[y.autor]].valor++;
      }
    }
    return estatisticas
  }

  AutorMaisPublicou() {
    let nome = '';
    let qt = 0;
    for (let x of this.noticiasPorAutor()){
      if (x.valor > qt) {
        nome = x.autor;
        qt = x.valor;
      }
    }
    return nome
  }

  quatidadeDeViews() {
    return this.dados.sort(function(a, b){return b.views-a.views})
  }

  quatidadeDeNoticias(valor) {
    let cont = 0;
    for (let noticia of this.dados) {
      if (noticia.autor === valor) {
        if (noticia.publicar === true) {
          cont++;
        }
      }
    }
    return cont;
  }

}
