import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { InicialComponent } from './inicial/inicial.component';
import { CadastroComponent } from './cadastro/cadastro.component';
import { PublicarComponent } from './publicar/publicar.component';
import { EstatisticasComponent } from './estatisticas/estatisticas.component';
import { AutorComponent } from './autor/autor.component';
import { VerComponent } from './ver/ver.component';


const routes: Routes = [
  {path: '', component: InicialComponent},
  {path: 'noticias', component: InicialComponent},
  {path: 'noticias/cadastrar', component: CadastroComponent},
  {path: 'noticias/publicar', component: PublicarComponent},
  {path: 'noticias/estatisticas', component: EstatisticasComponent},
  {path: 'noticias/autor/:id', component: AutorComponent},
  {path: 'noticias/:id', component: VerComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
